<template>
    <div id="root">
        Max Num of Pictures<br/>
        <md-button :class="[max == m ? 'md-raised' : '', theme]"
        v-for="m in maxes" :key="m" @click="setMax(m)">
            {{m}}
        </md-button><br/><br/>
        Image Source<br/>
        <md-button :class="[source == s ? 'md-raised' : '', theme]"
        v-for="s in sources" :key="s" @click="setSource(s)">
            {{s}}
        </md-button><br/><br/>
        Use System Title Bar(Needs Restart)<br />
        <md-button :class="[ifFrame ? 'md-raised' : '', theme]" @click="setIfFrame(true)">
        Yes
        </md-button>
        <md-button :class="[!ifFrame ? 'md-raised' : '', theme]" @click="setIfFrame(false)">
        No
        </md-button>
        <br/><br/>
    </div>
</template>

<script>
export default {
    props: {
        theme: String,
        rwConfig: Object
    },
    data () {
    },
    computed: {
        max () {return this.rwConfig.settings ? this.rwConfig.settings.max : null},
        maxes () {return this.rwConfig.settings ? this.rwConfig.settings.maxes : []},
        source () {return this.rwConfig.settings ? this.rwConfig.settings.source : null},
        sources () {return this.rwConfig.settings ? this.rwConfig.settings.sources : []},
        ifFrame () {return this.rwConfig.settings ? this.rwConfig.settings.ifFrame : false}
    },
    methods: {
        setMax (m) {
            if(m == this.max) return;
            this.$emit('ipc-event', {request: 'settings', params: {part: 'max', value: m}})
        },
        setSource (s) {
            if(s == this.source) return;
            this.$emit('ipc-event', {request: 'settings', params: {part: 'source', value: s}});
        },
        setIfFrame (f) {
            if(f == this.ifFrame) return;
            this.$emit('ipc-event', {request: 'settings', params: {part: 'ifFrame', value: f}});
        }
    },
}
</script>

<style scoped>
    #root {
        width: 100%;
        height: 100%;
        padding: 100px;
        overflow-y: scroll;
    }
</style>